/* src/app/components/typing-indicator/typing-indicator.css */
.typing-indicator {
  display: flex;
  align-items: center;
  height: 24px;
  padding-left: 8px;
}
.typing-indicator .dot {
  width: 8px;
  height: 8px;
  margin: 0 3px;
  border-radius: 50%;
  background-color: #4fa3f7;
  animation: blink 1.4s infinite both;
}
.typing-indicator .dot:nth-child(2) {
  animation-delay: 0.2s;
}
.typing-indicator .dot:nth-child(3) {
  animation-delay: 0.4s;
}
@keyframes blink {
  0%, 80%, 100% {
    transform: scale(0);
    opacity: 0.3;
  }
  40% {
    transform: scale(1);
    opacity: 1;
  }
}
